import React , { Fragment , useState , useEffect} from 'react';
import { Route, Redirect , useRouteMatch , useHistory } from 'react-router-dom';

import { PageLoader } from '../shared/components';
import { ProjectPage } from './Styles';
import  ProjectNavbarLeft from './NavbarLeft'
import  ProjectNavbarTop from './NavbarTop';
import  ProjectSidebar from './Sidebar';
import  OcclusionAndOrofacialPain from './OcclusionAndOrofacialPain';

const Project = () => {
    const match = useRouteMatch();
    const history = useHistory();

    const [stateIsLoading , setStateOpen] = useState(true);

    useEffect(() => {
        const timer = setTimeout(() => setStateOpen(false), 1000);
        return () => clearTimeout(timer);
      }, []);

    if(stateIsLoading){
        return <PageLoader/>
    }else
    {
        return (
            <ProjectPage>
                <ProjectNavbarLeft/>
                <ProjectNavbarTop/>
                <ProjectSidebar/>
                
                <Route
                    path={`${match.path}/occlusionandorofacialpain`}
                    render={() => (
                        <OcclusionAndOrofacialPain/>
                    )}
                />

                {match.isExact && <Redirect to={`${match.url}/occlusionandorofacialpain`} />}

            </ProjectPage>
        );   
    }
};

export default Project;
