import styled from 'styled-components';
import { color , font } from '../../utils/styles';

export const HiddenCheckbox = styled.input.attrs({type:'checkbox'})`
    border: 0;
    clip: rect(0 0 0 0);
    clippath: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
`;

export const Icon = styled.svg`
  fill: none;
  stroke: white;
  stroke-width: 2px;
`
export const StyledCheckbox = styled.div`
    display:flex;
    height: 20px;
    width:100%;
`;

export const LabelElement = styled.span`
    padding-left: 10px;
    color: ${color.textMedium};
    ${font.regular}
    ${font.size(13)}
    line-height: 19px;
    cursor:pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    -moz-user-select: none; 
    -ms-user-select: none; 
    user-select: none; 
`;


export const CheckboxElement = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    cursor:pointer;
    position: relative;
    width:18px;
    height:18px;
    border: 1px solid ${props => props.checked ? color.backgroundDarkPrimary : color.borderLightest};
    border-radius: 2px;
    background: ${props => props.checked ? color.backgroundDarkPrimary : "#fff"};
    transition: all 150ms;

    ${Icon} {
        visibility: ${props => props.checked ? 'visible' : 'hidden'}
    }
`;

