import React , { forwardRef , useState }  from 'react';
import PropTypes from 'prop-types';

import { StyledCheckbox , CheckboxElement , HiddenCheckbox , LabelElement , Icon } from './Styles';

const propTypes = {
    className: PropTypes.string,
    checkboxLable: PropTypes.string,
    checked: PropTypes.bool,
    onchange: PropTypes.func,
};

const defaultProps = {
    className: undefined,
    checkboxLable:'',
    checked: true,
    onChange: () => {},
}


const Checkbox = forwardRef(({className , checked , checkboxLable , onChange , ...inputProps} , ref ) =>{
    const [isChecked, setChecked] = useState(false);

    const handleChange = event =>{
        setChecked(event.target.checked);
        onChange(event.target.value, event);
    }

    return(
        <label>
            <StyledCheckbox className={className} onChange={handleChange}>
                <HiddenCheckbox/>
                <CheckboxElement checked={isChecked} {...inputProps}>
                <Icon viewBox="0 0 24 24">
                    <polyline points="20 6 9 17 4 12" />
                </Icon>
                </CheckboxElement>
                <LabelElement>{checkboxLable}</LabelElement>
            </StyledCheckbox>
        </label>
    );
});

Checkbox.propTypes = propTypes;
Checkbox.defaultProps = defaultProps;

export default Checkbox;
